import { Injectable } from '@angular/core';
import { discussionConfig } from './discussionList.interface';
import { Subject,BehaviorSubject } from 'rxjs';

@Injectable()
export class DiscussionListService {

    resultDiscussion: BehaviorSubject<any> = new BehaviorSubject<any>(null);;

    constructor() { }

    resetData(data) {
        let results = [];
        if (data && data.commentIds && data.commentIds.length) {
            data.commentIds.forEach(commentId => {
                const item = data['comments'].find((comment) => {
                    return comment["id"] === commentId
                })
                if (item) {
                    let id = item["id"],
                        userId = item["userId"],
                        userName = item['user']["name"],
                        //  imgData = item['user']["imgData"],
                        imgData = item['user'].hasOwnProperty('imgData') ? item['user']["imgData"] : '',
                        commentDate = item["commentDate"],
                        //  commentDate = this.dataFormate(item["commentDate"]),
                        text = item["text"],
                        attachFiles = item.hasOwnProperty('attachFiles') ? item["attachFiles"] : [],
                        parentData;
                    if (item.hasOwnProperty('parentId')) {
                        let parentItem = data['comments'].find(i => {
                            return i.id === item.parentId;
                        })
                        if (parentItem) {
                            let parid = parentItem["id"],
                                paruserId = parentItem["userId"],
                                paruserName = parentItem['user']["name"],
                                // parimgData = parentItem['user']["imgData"],
                                parimgData = parentItem['user'].hasOwnProperty('imgData') ? parentItem['user']["imgData"] : '',
                                parcommentDate = parentItem["commentDate"],
                                partext = parentItem["text"],
                                parattachFiles = parentItem.hasOwnProperty('attachFiles') ? parentItem["attachFiles"] : [];
                            parentData = {
                                id: parid,
                                userId: paruserId,
                                userName: paruserName,
                                imgData: parimgData,
                                commentDate: parcommentDate,
                                text: partext,
                                attachFiles: parattachFiles
                            }
                        }
                    }
                    let thisData: discussionConfig = {
                        id,
                        userId,
                        userName,
                        imgData,
                        commentDate,
                        text,
                        attachFiles,
                        parentData
                    };
                    results.push(thisData);
                }
                else {
                    return;
                }
            });
        }
        // this.resultDiscussion = results;
        this.resultDiscussion.next(results);
    }
    getData() {
        return this.resultDiscussion;
    }
}
