import { Pipe, PipeTransform } from '@angular/core';

@Pipe({name: 'avatarHtml'})
export class AvatarHtmlPipe implements PipeTransform {
    transform(item:any): string {
        let str = item['name'];
        if(str){
            return str.substring(str.length-2,str.length);
        }
        else{
            return '';
        }
    }
}

@Pipe({name: 'getOrgText'})
export class GetOrgTextPipe implements PipeTransform {
    transform(item:any, orgs: any[]): string {
        let str = item['orgId'];
        if(orgs && orgs.length){
            let sec = orgs.find(org => {
                return org['id'] == str;
            });
            let orgtext = '';
            if(sec){
                if(sec['parentId'] != ''){
                    let par = orgs.find(org => {
                        return org['id'] == sec['parentId'];
                    });
                    orgtext = par ? `${par.name}/${sec['name']}`:sec['name'];
                }
                else{
                    orgtext = sec['name'];
                }
            }
            return orgtext;
        }
        else {
            return '';
        }
    }
}
